import React, {useState} from 'react';
import {faFire, faHome, faStickyNote, faWrench, IconDefinition} from '@fortawesome/free-solid-svg-icons'
import {HeaderPrimary, TextPrimary, TextPrimary_Center, TextPrimary_White} from '../../Components';
import {
    Action,
    BordererPresets,
    BreakpointLarge,
    BreakpointMedium,
    BreakpointSmall,
    BreakpointXL,
    FlexDirection,
    Flexer,
    FlexerPresets,
    FlexMainAxis,
    FlexSecondaryAxis,
    FlexWrap,
    Hover,
    Icon, Marginer,
    MarginerPresets,
    Shadower, shiftBreakpoint,
    Surface,
    useBreakpoint
} from '@spinglobal/react-foundations';
import {Modal} from '../../Components';
import {HeaderTertiary} from "../../Components";
import {ColorSet} from "../../styles";

export function ExpertiseLogo({icon}: { icon: IconDefinition }) {
    return <Surface width={120} height={120} background={ColorSet.Secondary3} style={{
        ...BordererPresets.P50,
        ...FlexerPresets.Center
    }}>
        <Icon fill='white' size={48} icon={icon}/>
    </Surface>;
}

export interface ExpertiseCardProps {
    icon: IconDefinition;
    title: string;
}

export function ExpertiseCard({icon, title}: ExpertiseCardProps) {
    const [open, setOpen] = useState<boolean>(false);
    const width = useBreakpoint() <= BreakpointSmall ? '100%' : 520;
    const height = 320;

    return <Action onClickCapture={() => setOpen(true)}>
        <Surface width={width} height={height} background={ColorSet.Primary2}
                 style={{
                     ...Marginer({top: 6}),
                     ...Shadower({voffset: 30, blur: 90}),
                     ...FlexerPresets.Center
                 }}>
            <Modal background='rgba(0, 0, 0, .8)' open={open} onClickCapture={() => {
                setOpen(false)
            }}>
                <ExpertiseLogo icon={icon}/>
            </Modal>
            <ExpertiseLogo icon={icon}/>
            <HeaderTertiary
                text={title}
                style={MarginerPresets._3}
            />
            <Hover hoverStyles={{backgroundColor: ColorSet.Secondary3}}>
                <Surface style={{
                    ...BordererPresets._4,
                    ...Shadower({hoffset: 1, voffset: 1, blur: 2}),
                    ...FlexerPresets.Center
                }}>
                    <TextPrimary_White
                        text='Learn More'
                        style={MarginerPresets._0}
                    />
                </Surface>
            </Hover>
        </Surface>
    </Action>;
}

export function ServicesExpertise() {
    const bp = useBreakpoint();
    const direction = bp <= BreakpointMedium ? FlexDirection.Column : FlexDirection.Row;
    const paragraph1 = 'AC Disaster Consulting is a full-spectrum emergency management firm. Our team of experts provide high-quality consulting services in the following areas';
    return <Surface style={{...FlexerPresets.ColumnCenter, ...Marginer({bottom: 7})}}>
        <HeaderPrimary
            text='Services & Expertise'
            style={MarginerPresets._3}/>
        <TextPrimary_Center
            text={paragraph1}
            style={{
                maxWidth: shiftBreakpoint(bp, -2),
            }}/>
        <Surface style={{
            maxWidth: shiftBreakpoint(bp, -1),
            ...Flexer({
                direction,
                main: FlexMainAxis.SpaceAround,
                secondary: FlexSecondaryAxis.Center,
                wrap: FlexWrap.Wrap
            })
        }}>
            <ExpertiseCard icon={faFire} title={'Planning & Preparedness'}/>
            <ExpertiseCard icon={faStickyNote} title={'Response Operations & Logistics'}/>
            <ExpertiseCard icon={faHome} title={'Recovery'}/>
            <ExpertiseCard icon={faWrench} title={'Mitigation'}/>
        </Surface>
    </Surface>
}
